* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: Arial;
  font-weight: bold;
}

body {
  background-color: #5cb85c;
}

a {
  display: block;
}

h1 {
  font-family: Baloo Da;
}

h5{
  text-align: center;
}

#title {
  color: white;
  text-align: center;
}

.container {
  margin: 3% auto;
  background-color: #5cb85c;
  padding: 10px;
  max-width: 450px;
}

#dimmer {
  background: #333;
  position: fixed;
  z-index: 1;
  opacity: .4;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

#modal {
  background: #DDD;
  padding: 10px;
  position: fixed;
  margin-top: -150px;
  margin-left: -150px;
  top: 30%;
  left: 50%;
  border-radius: 5px;
  width: 300px;
  z-index: 9999;
}

input {
  width: 100%;
  font-weight: normal;
}

textarea {
  width: 100%;
  resize: none; 
  font-weight: normal;
}

.list {
  padding-bottom: 10px;
}

.list-item {
  font-size: 1em;
  margin: 5px;
  padding: 10px;
  background-color: #CCC;
  border-radius: 2px;
  text-align: center;
}

.list-item:hover {
  background-color: #AAA;
}

.direction {
  display: inline-block;
}

#credits {
  color: white;
  text-align: center;
}




